import React from 'react'
import moment from 'moment'
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";


const EditExpectedUpdate = ({value, change}) => {

  const expectedUpdate = value && value.seconds ? moment.unix(value.seconds).toDate() : value ? value : null

  const dateChange = (newDate,g) => {
    var dateToSend = newDate
    if(dateToSend !== null){
      change(dateToSend)
    }
  }

  const focus = (e) => {
    e.target.select()
  }

  return ( 
    <div className="mt-2 flex space-x-2">
      <p className="font-bold text-md">Due:</p>
      <DatePicker id='expectedUpdate' isClearable={false} className='cursor-pointer hover:underline' selected={expectedUpdate} onFocus={focus} onChange={dateChange} />
    </div>
   );
}
 
export default EditExpectedUpdate;